@import '../../variables.scss';
@import '../../mixin.scss';

$yellowBg: #FF8E3D;
$blackBg: #373737;
$nftGreen: #08EDA8;
$nftGreenHover: #a0f3d5;
$nftGreenPress: #61d29c;
$nftGreenDisabled: #b5b7c0;
$nftBlue: #506CFF;
$nftPurple: #9634F9;
$nftGrey: #606060;
$tagYellow: #FFED4B;
$tagGreen: #18F1D7;
$tagPurple: #FB2FFF;
$tagGrey1: #7F8393;
$tagGrey2: #494E67;


dl,
dt,
dd,
p,
h1,
h2,
h3,
h4,
h5 {
  margin: 0;
  padding: 0;
}

.modalHeader2 {
  font-family: "Barlow";
  font-size: 24px;
  color: black;
  font-weight: 700;
  margin: 0.5em 0;
}

.modalTips {
  font-family: "Barlow";
  font-size: 16px;
  color: black;
  font-weight: 700;
  margin: 1em 0;
}

.pcs {
  background: $PureBlack;
  color: $PureWhite;
  font-weight: 700;
  font-size: 18px;
  line-height: 1;
  padding: 8px 12px;
  border: 1px solid $PureBlack;
  word-break: break-word;
}

.commonTag {
  padding: 5px 12px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  border: 1px solid $PureBlack;
  text-transform: uppercase;
  line-height: 1;

  >img {
    width: 24px;
    height: 24px;
  }

  >span {
    margin-left: 10px;
    font-weight: 700;
    font-size: 18px;
    color: $PureBlack;
  }
}

.legendaryTag {
  background: $tagYellow;
}

.epicTag {
  background: $tagPurple;
}

.normalTag {
  background: $PureWhite;
}

.topRewards {
  padding: 8px 12px;
  background: $tagGreen;
  border: 1px solid $PureBlack;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 18px;
  color: $PureBlack;
  line-height: 1;
  word-break: break-word;
}

.BorderedContainerTitleBarContent {
  position: absolute;
  top: 0;
  color: white;
  width: 100%;
  height: 60px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 1em;

  .title {
    font-family: "Barlow";
    font-weight: 700;
    font-size: 20px;
    color: #FF8E3D;
  }

  .buyMore {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    transition: transform .4s ease;
    cursor: pointer;
    z-index: 1;
    margin-right: 2em;

    &:hover {
      transform: translateY(-2px);
    }

    >span {
      color: $nftGreen;
      text-decoration: underline;
      font-size: 18px;
      line-height: 1.2;
    }

    >img {
      margin-left: 6px;
      width: 16px;
      height: 16px;
    }
  }
}

.nftContainer {
  min-height: 100vh;
  line-height: 1.2;

  .inner {
    position: relative;
    width: 1212px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .wireWrap {
    width: 1212px;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: calc((100% - 1212px) / 2);

    .wire {
      position: absolute;
      top: 0;
      bottom: 0;
      width: 1px;
      background: $PureWhite;
      opacity: 0.3;

      &:nth-of-type(1) {
        left: 0;
      }

      &:nth-of-type(2) {
        left: 416px;
      }

      &:nth-of-type(3) {
        left: 831px;
      }

      &:nth-of-type(4) {
        right: 0;
      }
    }
  }

  .titleWrap {
    width: 100%;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;

    .title {
      position: relative;
      font-weight: 700;
      font-size: 48px;
      line-height: 1;
      color: $PureWhite;
      z-index: 2;
      text-align: center;
    }

    .titleStroke {
      position: absolute;
      bottom: -2px;
      left: 0;
      width: 100%;
      font-size: 86.34px;
      font-weight: 700;
      color: $blackBg;
      text-shadow: -1px 0 $PureWhite,
        0 1px $PureWhite,
        1px 0 $PureWhite,
        0 -1px $PureWhite;
      opacity: 0.5;
      line-height: 1;
      z-index: 1;
      text-align: center;
    }
  }

  .mint {
    width: 273px;
    height: 78px;
    line-height: 78px;
    text-align: center;
    background: url('../../images/freshBank/nft/mintBtn.svg') no-repeat 0 0;
    font-weight: 700;
    font-size: 28px;
    color: $PureBlack;
    border: 0 none;
    text-decoration: none;

    &:active {
      background-image: url('../../images/freshBank/nft/mintBtn_press.svg');
    }

    &:disabled {
      transition: initial;
      cursor: not-allowed;
      background-image: url('../../images/freshBank/nft/mintBtn_disabled.svg');
    }
  }

  // common css end
  .container {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: $PureBlack;
  }


  .countDownContainer {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 0;
    padding-bottom: 88px;

    .sloganBg {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: calc(100% + 3000px);
      background: $PureBlack url('../../images/freshBank/nft/bg.svg') no-repeat center bottom;
      z-index: -1;
    }

    .slogan {
      margin-top: 60px;
      color: $PureWhite;
      font-weight: 700;
      font-size: 48px;
    }

    .stage {
      display: flex;
      flex-direction: column;
      align-items: center;

      .desc {
        margin-top: 35px;
        color: $PureWhite;
        font-weight: 700;
        font-size: 24px;
        text-align: center;
      }

      .desc1 {
        margin-top: 12px;
        color: black;
        font-weight: 700;
        font-size: 20px;

        span {
          color: #FF8E3D;
          font-size: 24px;
          padding: 0 2px;
        }
      }

      .dountDown {
        display: flex;
        flex-direction: row;
        align-items: center;
        color: $PureWhite;
        margin-top: 32px;

        dl {
          position: relative;
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          width: 80px;
          height: 102px;
          background: $PureBlack;
          clip-path: polygon(16px 0%, 100% 0%, 100% calc(100% - 16px), calc(100% - 16px) 100%, 0% 100%, 0% 16px);

          &:after {
            content: '';
            position: absolute;
            top: 32px;
            left: 0;
            right: 0;
            background: $PureBlack;
            height: 3px;
            z-index: 1;
          }

          dt {
            font-weight: 500;
            font-size: 20.27px;
            margin-top: 13px;

            &:after {
              content: '';
              position: absolute;
              left: 4px;
              bottom: 4px;
              right: 4px;
              background: rgba($PureWhite, .2);
              clip-path: polygon(0% 0%, 100% 0%, 100% calc(100% - 16px), calc(100% - 16px) 100%, 0% 100%, 0% 16px);
              height: 29px;
              border-top: 2px solid rgba($PureWhite, .3);
            }
          }

          dd {
            font-weight: 700;
            font-size: 44px;
          }
        }

        .colon {
          display: flex;
          font-weight: 500;
          font-size: 48px;
          margin: 0 14px;
          color: $PureBlack;
          margin-bottom: 14px;
          line-height: 1;
        }
      }

      .mint {
        margin-top: 40px;
      }
    }

  }

  .roadMapAndAboutContainer {
    width: 100%;
    z-index: 1;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 63px 20px 80px 20px;
    background: $yellowBg url('../../images/freshBank/nft/dotBg.svg') repeat 0 0;

    &:before {
      content: '';
      position: absolute;
      top: -39px;
      left: 0;
      width: 100%;
      height: 39px;
      background: url('../../images/freshBank/nft/vectorTop.svg') repeat-x 0 0;
    }

    &:after {
      content: '';
      position: absolute;
      bottom: -38px;
      left: 0;
      width: 100%;
      height: 39px;
      background: url('../../images/freshBank/nft/vectorBottom.svg') repeat-x 0 0;
    }

    .roadMapContainer {
      width: 100%;

      .roadMapList {
        position: relative;
        display: flex;
        flex-direction: row;
        max-width: 1200px;
        width: 100%;
        margin: 0 auto;
      }

      .line {
        position: absolute;
        left: 50%;
        top: 8px;
        width: 100%;
        height: 1px;
        background: $PureBlack;
        transform: translateX(-50%);
      }

      .roadMapItem {
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;

        .circle {
          position: relative;
          width: 16px;
          height: 16px;
          background: $yellowBg;
          border: 1px solid $PureBlack;
          border-radius: 50%;

          &:after {
            content: '';
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            width: 8px;
            height: 8px;
            background: $PureBlack;
            border-radius: 50%;
          }
        }

        .subjectWrap {
          margin-top: 22px;
          display: flex;
          flex-direction: column;
          align-items: center;

          .subject {
            font-weight: 500;
            font-size: 18px;
            color: $PureBlack;
            text-align: center;
          }

          .subjectTime {
            font-weight: 400;
            font-size: 18px;
            margin-top: 10px;
          }
        }

      }
    }

    .aboutContainer {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;

      .titleWrap {
        margin-top: 84px;

        .title {
          color: $PureBlack;
        }

        .titleStroke {
          color: $yellowBg;
          font-size: 66.14px;
        }
      }

      .aboutIntroContainer {
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        flex-wrap: wrap;
        margin-top: 56px;

        .introWrap {
          position: relative;
          width: 400px;
          min-height: 420px;
          border: 1px solid $PureBlack;
          clip-path: polygon(0% 0%, calc(100% - 44px) 0%, 100% 44px, 100% 100%, 44px 100%, 0% calc(100% - 44px), 0% 0%);
          margin-right: 32px;
          padding: 32px 28px;

          &:before {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            bottom: 0;
            width: 43px;
            height: 43px;
            background-color: $PureBlack;
            clip-path: polygon(0% 0%, 0% 100%, 100% 100%);
          }

          &:after {
            content: '';
            display: block;
            position: absolute;
            right: 0;
            top: 0;
            width: 43px;
            height: 43px;
            background-color: $PureBlack;
            clip-path: polygon(0% 0%, 100% 0%, 100% 100%);
          }

          dt {
            position: relative;
            font-size: 44px;
            font-weight: 700;
            color: $DarkBlack;
            text-transform: uppercase;

            &::before {
              content: '';
              position: absolute;
              right: 0;
              bottom: -16px;
              width: 100%;
              height: 1px;
              background: $PureBlack;
            }

            &::after {
              content: '';
              position: absolute;
              left: 0;
              bottom: -20px;
              width: 62px;
              height: 5px;
              background: $PureBlack;
              clip-path: polygon(0% 0%, 100% 0%, 80% 100%, 0% 100%);
            }
          }

          dd {
            margin-top: 40px;
            font-weight: 400;
            font-size: 14px;
            color: $PureBlack;
            line-height: 2;

            p {
              padding-top: 20px;

              &:first-child {
                padding-top: 0;
              }
            }
          }
        }

        .picWrap {
          position: relative;
          width: 400px;
          min-height: 420px;

          .picInner {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 400px;
            background: $PureBlack;
            clip-path: polygon(0% 0%, calc(100% - 44px) 0%, 100% 44px, 100% 100%, 44px 100%, 0% calc(100% - 44px), 0% 100%);

            >img {
              width: 400px;
            }
          }

          &::before {
            content: '';
            position: absolute;
            right: 0;
            bottom: 0;
            width: calc(100% - 44px);
            height: 1px;
            background: $PureBlack;
          }

          &::after {
            content: '';
            position: absolute;
            left: 44px;
            bottom: 0;
            width: 48px;
            height: 5px;
            background: $PureBlack;
          }
        }


      }
    }

    .mintTipsContainer {
      position: relative;
      margin-top: 40px;
      max-width: 832px;
      width: 100%;
      border: 1px solid $PureBlack;
      clip-path: polygon(0% 0%, calc(100% - 40px) 0%, 100% 40px, 100% 100%, 40px 100%, 0% calc(100% - 40px), 0% 0%);
      padding: 10px;

      &:before {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 39px;
        height: 39px;
        background-color: $PureBlack;
        clip-path: polygon(0% 0%, 0% 100%, 100% 100%);
      }

      &:after {
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        width: 39px;
        height: 39px;
        background-color: $PureBlack;
        clip-path: polygon(0% 0%, 100% 0%, 100% 100%);
      }

      .mintTipsInner {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        background: $PureBlack;
        clip-path: polygon(0% 0%, calc(100% - 38px) 0%, 100% 38px, 100% 100%, 38px 100%, 0% calc(100% - 38px), 0% 0%);
        padding: 20px 54px 26px 28px;

        &:after {
          content: '';
          position: absolute;
          right: 16px;
          bottom: 12px;
          background: url('../../images/freshBank/nft/arrowDecor_icon.svg') no-repeat 0 0;
          width: 51px;
          height: 10px;
        }

        .mintTips {
          display: flex;
          flex-direction: row;
          align-items: center;

          >img {
            width: 32px;
          }

          .txtWrap {
            flex: 1 0 60px;
            margin-left: 28px;

            .paragraph {
              color: $PureWhite;
              line-height: 1.57;
              font-weight: 400;
              font-size: 14px;

              >span {
                font-size: 20px;
                color: $yellowBg;
                font-weight: 700;
                font-style: normal;
                padding: 0 2px;
              }
            }
          }
        }

        .torchlightAddWallet {
          position: relative;
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-self: center;
          margin-left: 60px;
          margin-top: 12px;
          background: $nftGreen;
          padding: 6px 16px;
          border: 0 none;
          outline: 0 none;
          cursor: pointer;
          min-height: 40px;
          clip-path: polygon(0% 0%, calc(100% - 10px) 0%, 100% 10px, 100% 100%, 10px 100%, 0% calc(100% - 10px), 0% 0%);

          &:active {
            background: $nftGreenPress;
          }

          >span {
            color: $PureBlack;
            font-weight: 400;
            font-size: 14px;
          }

          >img {
            margin-left: 10px;
            width: 17px;
          }
        }
      }
    }
  }

  .forgeContainer {
    width: 1212px;
    padding: 2em 0;

    .forgeLayout {
      width: 100%;
      margin-top: 68px;

      .notificationBar {
        background-color: #FF8E3D;
        color: black;
        padding: 1em;
        font-family: "Barlow";
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
      }

      .split {
        border-left: 1px solid #FF8E3D;
        border-right: 1px solid #FF8E3D;
        border-bottom: 1px solid #FF8E3D;
        padding: 0.5em;
        background-image: url('../../images/freshBank/nft/split.svg');
        background-repeat: repeat-x;
        background-position: 1em;
      }

      .contentContainer {
        padding: 1em;
        height: 240px;

        div {
          width: 100%;
          display: flex;
          flex-direction: row;
          align-items: flex-start;
          justify-content: space-between;

          .subContainer {
            width: calc(50% - 0.5em);

            .launch {
              width: 100%;
              height: 190px;
              display: flex;
              justify-content: center;
              align-items: center;

              div {
                width: 95%;
                line-height: 1.5;
                display: flex;
                justify-content: center;
                align-items: center;
                font-family: "Barlow";
                font-weight: 700;
                font-size: 16px;
                color: white;
                flex-direction: column;

                button {
                  margin-top: 1.5em;
                }
              }
            }

            .recentlyForge {
              padding: 0 1.5em;
              display: flex;
              flex-direction: column;
              height: 190px;

              .title {
                font-family: "Barlow";
                font-weight: 700;
                font-size: 20px;
                color: #FF8E3D;
                margin-bottom: 1em;
                text-transform: capitalize;
              }

              .list {
                display: flex;
                flex-direction: column;

                .listItem {
                  display: flex;
                  flex-direction: row;
                  justify-content: space-between;
                  font-family: "Barlow";
                  font-weight: 700;
                  font-size: 14px;
                  color: white;
                  margin: 0.45em 0;
                  width: 100%;

                  .accountLabel{
                    width: 30%;
                  }

                  .itemLabel {
                    display: flex;
                    justify-content: flex-end;
                    width: 70%;
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  .galleryContainer {
    width: 100%;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: url('../../images/freshBank/nft/galleryBg.svg') no-repeat center center;

    .galleryInner {
      padding-top: 171px;
      padding-bottom: 86px;
    }

    .buy {
      position: absolute;
      top: 190px;
      right: 0;
      display: flex;
      flex-direction: row;
      align-items: flex-end;
      transition: transform .4s ease;
      cursor: pointer;
      z-index: 1;

      &:hover {
        transform: translateY(-2px);
      }

      >span {
        color: $nftGreen;
        text-decoration: underline;
        font-size: 20px;
        line-height: 1.2;
      }

      >img {
        margin-left: 6px;
        width: 16px;
        height: 16px;
      }
    }

    .titleWrap {
      position: relative;
    }

    .listWrap {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      align-items: stretch;
      justify-content: space-between;
      margin-top: 66px;
      padding: 0 13px;
      width: 100%;

      .itemWrapOuter {
        position: relative;
        width: 341px;
        min-height: 516px;

        &:before {
          content: '';
          position: absolute;
          top: 15px;
          left: -13px;
          width: 13px;
          height: 137px;
          background: url('../../images/freshBank/nft/galleryPurple_left.svg') no-repeat 0 0;
        }

        &:after {
          content: '';
          position: absolute;
          bottom: 80px;
          right: -13px;
          width: 13px;
          height: 77px;
        }

        &.itemWrapOuterPurple {
          &:before {
            background: url('../../images/freshBank/nft/galleryPurple_left.svg') no-repeat 0 0;
          }

          &:after {
            background: url('../../images/freshBank/nft/galleryPurple_right.svg') no-repeat 0 0;
          }

          .itemWrap {
            background: $nftPurple;
          }
        }

        &.itemWrapOuterBlue {
          &:before {
            background: url('../../images/freshBank/nft/galleryBlue_left.svg') no-repeat 0 0;
          }

          &:after {
            background: url('../../images/freshBank/nft/galleryBlue_right.svg') no-repeat 0 0;
          }

          .itemWrap {
            background: $nftBlue;
          }
        }

        &.itemWrapOuterGreen {
          &:before {
            background: url('../../images/freshBank/nft/galleryGreen_left.svg') no-repeat 0 0;
          }

          &:after {
            background: url('../../images/freshBank/nft/galleryGreen_right.svg') no-repeat 0 0;
          }

          .itemWrap {
            background: $nftGreen;
          }
        }

        &.itemWrapOuterGrey {
          &:before {
            background: url('../../images/freshBank/nft/galleryGrey_left.svg') no-repeat 0 0;
          }

          &:after {
            background: url('../../images/freshBank/nft/galleryGrey_right.svg') no-repeat 0 0;
          }

          .itemWrap {
            background: $nftGrey  !important;
          }

          .topRewards,
          .commonTag,
          .pcs {
            background: rgba($PureWhite, .3) !important;
            color: $PureBlack  !important;
          }

          .ownedNum {
            display: none !important;
          }

          .notOwned {
            display: block !important;
          }

          .galleryPic {
            filter: grayscale(100%);
          }
        }
      }

      .itemWrap {
        width: 100%;
        height: 100%;
        position: relative;
        clip-path: polygon(0% 0%, calc(100% - 44px) 0%, 100% 44px, 100% 100%, 44px 100%, 0% calc(100% - 44px), 0% 100%);
        padding: 4px;

        .itemWrapInner {
          position: relative;
          width: 100%;
          height: 100%;
          border: 1px solid $PureBlack;
          clip-path: polygon(0% 0%, calc(100% - 42px) 0%, 100% 42px, 100% 100%, 42px 100%, 0% calc(100% - 42px), 0% 100%);

          .ownedNum {
            position: absolute;
            top: 6px;
            left: 6px;
            padding: 9px 20px 9px 16px;
            background: $nftGreen;
            font-size: 18px;
            font-weight: 700;
            color: $PureBlack;
            z-index: 1;
            clip-path: polygon(0% 0%, 100% 0%, 100% calc(100% - 10px), calc(100% - 10px) 100%, 0% 100%);
            text-transform: uppercase;
          }

          &:after {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            bottom: 0;
            width: 41px;
            height: 41px;
            background: $PureBlack;
            clip-path: polygon(0% 0%, 0% 100%, 100% 100%);
          }
        }

        .galleryPicWrap {
          position: relative;
          width: 100%;

          .galleryPic {
