.btnAction {
  position: relative;
  font-size: 14px;
  line-height: 1.2;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid $PureWhite;
  color: $PureWhite;
  background: transparent;
  padding: 10px;
  min-width: 106px;
  clip-path: polygon(0% 0%, calc(100% - 10px) 0%, 100% 10px, 100% 100%, 10px 100%, 0% calc(100% - 10px), 0% 0%);
  text-transform: capitalize;

  &::before {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 9px;
    height: 9px;
    background-color: $PureWhite;
    clip-path: polygon(0% 0%, 0% 100%, 100% 100%);
  }

  &:after {
    content: '';
    position: absolute;
    right: 0;
    top: 0;
    width: 9px;
    height: 9px;
    background-color: $PureWhite;
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%);
  }

  &.normal {
    background-color: $nftGreen;
    border-color: $nftGreen;
    color: $PureBlack;
    pointer-events: all;
  }

  &.now {
    background: $nftGreen;
    border-color: $nftGreen;
    color: $PureBlack;
    pointer-events: all;

    &:active {
      background: $nftGreenPress;
      border-color: $nftGreenPress;
    }

    &::before,
    &::after {
      background-color: $nftGreen;
    }
  }

  &.onBoosting,
  &.past {
    background: rgba($yellowBg, .2);
    border-color: $yellowBg;
    color: $yellowBg;
    opacity: .5;
    cursor: not-allowed;
    pointer-events: none;

    &::before,
    &::after {
      background-color: $yellowBg;
    }
  }

  &.waiting {
    background: rgba($tagGrey2, .2);
    border-color: $tagGrey2;
    color: $tagGrey2;
    cursor: not-allowed;
    pointer-events: none;

    &::before,
    &::after {
      background-color: $tagGrey2;
    }
  }
}

