.nftBurnModal {
  .bodyContainer {
    .bodyContent {
      padding-bottom: 20px;

      .boosMultiWrap {
        width: 100%;
        display: flex;
        flex-direction: row;
        align-items: center;

        .bmTitle {
          font-weight: 500;
          font-size: 18px;
          color: $PureBlack;
          margin-right: 12px;
        }

        .numChange {
          display: flex;
          flex-direction: row;
          justify-content: center;
          align-items: center;
          height: 36px;
          padding: 0 16px;
          background: $PureBlack;

          >img {
            width: 14.25px;
            margin: 0 10px;
          }

          .oldNum,
          .newNum {
            font-weight: 700;
            font-size: 24px;
          }

          .oldNum {
            color: $PureWhite;
          }

          .newNum {
            color: $nftGreen;
          }
        }
      }

      .burnList {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: flex-start;
        width: 100%;
        margin-top: 33px;
        margin-bottom: 24px;

        .burnItem {
          width: 143px;
          margin: 0 0.5em;

          .picWrap {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            width: 100%;
            height: 100px;
            border: 1px solid $PureBlack;

            &.picYellowWrap {
              background: $tagYellow;
            }

            &.picPurpleWrap {
              background: $tagPurple;
            }

            &.picNormalWrap {
              background: $PureWhite;
            }

            >img {
              width: 24px;
              margin-bottom: 8px;
            }

            >span {
              text-transform: uppercase;
              font-weight: 700;
              font-size: 18px;
            }
          }

          .burnCalc {
            margin-top: 16px;
            display: flex;
            flex-direction: row;
            width: 100%;
            align-items: center;
            justify-content: space-between;

            .sub,
            .add {
              position: relative;
              width: 32px;
              height: 32px;
              border: 1px solid $PureBlack;
              background: $nftGreen;
              cursor: pointer;

              &::before {
                content: '';
                position: absolute;
                width: 16px;
                height: 2px;
                background: $PureBlack;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%);
              }

              &.disabled {
                background: $LGrey;
                cursor: not-allowed;
                pointer-events: none;
              }
            }

            .add {
              &::after {
                content: '';
                position: absolute;
                width: 16px;
                height: 2px;
                background: $PureBlack;
                left: 50%;
                top: 50%;
                transform: translate(-50%, -50%) rotate(90deg);
              }
            }

            input {
              width: 63px;
              height: 32px;
              background: $PureBlack;
              text-align: center;
              color: $PureWhite;
              font-weight: 700;
              font-size: 18px;
              outline: 0 none;
              border: 0 none;
              appearance: textfield;

              &::-webkit-inner-spin-button {
                appearance: none;
              }

              &::-webkit-outer-spin-button {
                appearance: none;
              }
            }
          }
        }
      }

      .burnTips {
        display: flex;
        flex-direction: row;
        align-items: center;

        >img {
          width: 20px;
          height: 20px;
          margin-right: 8px;
        }

        >span {
          font-weight: 500;
          font-size: 16px;
          color: $PureBlack;
          text-align: left;
        }
      }
    }
  }
}


