.infiniteListLayout {
	overflow: hidden;
	position: relative;
	width: 100%;
	height: 100%;

	.content {
		position: absolute;
		width: 100%;
		height: fit-content;
		top: 0;
		left: 0;
		transition: all 0.5s;
		display: flex;
		flex-direction: column !important;
	}

	.contentWithoutTransation {
		position: absolute;
		width: 100%;
		height: fit-content;
		top: 0;
		left: 0;
		transition: none !important;
		display: flex;
		flex-direction: column !important;
	}
}

@media (max-width: 575.98px) {
	.infiniteListLayout {
		height: 190px;
	}	
}