@import '../../../variables.scss';
@import '../../../mixin.scss';

$yellowBg: #FF8E3D;
$blackBg: #373737;
$nftGreen: #08EDA8;
$nftGreenHover: #a0f3d5;
$nftGreenPress: #61d29c;
$nftGreenDisabled: #b5b7c0;
$nftBlue: #506CFF;
$nftPurple: #9634F9;
$nftGrey: #606060;
$tagYellow: #FFED4B;
$tagGreen: #18F1D7;
$tagPurple: #FB2FFF;
$tagGrey1: #7F8393;
$tagGrey2: #494E67;

.borderedContainer {
	width: 100%;
	position: relative;
	display: flex;
	flex-direction: column !important;
	background-color: black;

	.titleBar {
		width: 100%;

		.forgeBG {
			width: 100%;

			svg {
				z-index: 0;
			}

			svg path {
				// stroke-width: 1px;
			}
		}

		.content {
			position: absolute;
			top: 0;
			color: white;
			width: 100%;
			height: 60px;
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			align-items: center;
			padding: 1em;

			.title {
				font-family: "Barlow";
				font-weight: 700;
				font-size: 20px;
				color: #FF8E3D;
			}

			.buyMore {
				display: flex;
				flex-direction: row;
				align-items: flex-end;
				transition: transform .4s ease;
				cursor: pointer;
				z-index: 1;
				margin-right: 2em;

				&:hover {
					transform: translateY(-2px);
				}

				>span {
					color: $nftGreen;
					text-decoration: underline;
					font-size: 18px;
					line-height: 1.2;
				}

				>img {
					margin-left: 6px;
					width: 16px;
					height: 16px;
				}
			}
		}
	}

	.contentContainer {
		border-left: 1px solid #FF8E3D;
		border-right: 1px solid #FF8E3D;
		padding: 0;
		// height: 200px;
	}

	.footBar {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start;

		.forgeBG {
			width: 100%;
			display: flex;
			flex-direction: column;
			justify-content: flex-start;

			svg {
				z-index: 0;
			}

			svg path {
				// stroke-width: 1px;
			}
		}
	}
}

@media (max-width: 575.98px) {
	.borderedContainer {
		.titleBar {
			.forgeBG {
				svg path {
					stroke-width: 5px;
				}
			}
		}

		.footBar {
			.forgeBG {
				svg path {
					stroke-width: 5px;
				}
			}
		}
	}
}